.submission-viewer {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 20px;
  background: #f9f9f9;
  border-radius: 8px;
  max-height: 600px;
  overflow-y: auto;

  .content-section {
    display: flex;
    flex-direction: column;
    gap: 10px;

    .section-title {
      display: flex;
      align-items: center;
      gap: 8px;
      font-weight: 600;
      font-size: 14px;
      color: #333;

      mat-icon {
        font-size: 18px;
        width: 18px;
        height: 18px;
        color: #1976d2;
      }
    }

    .content-box,
    .text-content,
    .link-box,
    .code-box,
    .feedback-box {
      padding: 12px;
      background: white;
      border: 1px solid #e0e0e0;
      border-radius: 4px;
      font-size: 13px;
      line-height: 1.6;
      color: #666;
    }

    .text-content {
      white-space: pre-wrap;
      word-wrap: break-word;
    }

    .link-box {
      a {
        color: #1976d2;
        text-decoration: none;
        display: flex;
        align-items: center;
        gap: 8px;

        &:hover {
          text-decoration: underline;
        }

        mat-icon {
          font-size: 14px;
          width: 14px;
          height: 14px;
        }
      }
    }

    .code-box {
      background: #f5f5f5;
      font-family: 'Courier New', monospace;
      overflow-x: auto;

      pre {
        margin: 0;
        code {
          font-size: 12px;
          color: #333;
        }
      }
    }

    .file-list {
      display: flex;
      flex-direction: column;
      gap: 8px;

      .file-item {
        display: flex;
        align-items: center;
        gap: 10px;
        padding: 10px;
        background: white;
        border: 1px solid #e0e0e0;
        border-radius: 4px;

        .file-icon {
          color: #1976d2;
          font-size: 20px;
          width: 20px;
          height: 20px;
          flex-shrink: 0;
        }

        .file-name {
          flex: 1;
          font-size: 13px;
          color: #666;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
      }
    }

    .feedback-box {
      background: #e8f5e9;
      border-color: #4caf50;
      color: #2e7d32;
    }
  }
}
